import { combineReducers } from 'redux'

//
const Locationcomment = (state = [], action) => {
  switch (action.type) {
    case 'GET_LOCATIONCOMMENT':
      return action.value
    default:
      return state
  }
}
//
const Blogcomment = (state = [], action) => {
  switch (action.type) {
    case 'GET_BLOGCOMMENT':
      return action.value
    default:
      return state
  }
}
const donelocationcomment = (state = [], action) => {
  switch (action.type) {
    case 'SENT_COMMENT':
      return action.value
    default:
      return state
  }
}
const commentReducer = combineReducers({
  Locationcomment,
  Blogcomment,
  donelocationcomment,
})
export { commentReducer }
