import { combineReducers } from 'redux'

//
const eventData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_EVENTDATA':
      return action.value
    default:
      return state
  }
}

//()
const eventDataForMap = (state = {}, action) => {
  switch (action.type) {
    case 'GET_EVENTDATA_MAP':
      return action.value
    default:
      return state
  }
}

//
const eventTypeData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_EVENT_TYPEDATA':
      return action.value
    default:
      return state
  }
}

//
const eventDetailData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_EVENT_DETAIL_DATA':
      return action.value
    default:
      return state
  }
}

//
const memberEventDataSelf = (state = {}, action) => {
  switch (action.type) {
    case 'GET_MEMBER_EVENT_SELF':
      return action.value
    default:
      return state
  }
}

//CRUD
const memberActionResponse = (state = {}, action) => {
  switch (action.type) {
    case 'ADD_EVENTDATA':
      return action.value
    case 'EDIT_EVENTDATA':
      return action.value
    case 'DEL_EVENTDATA':
      return action.value
    case 'APPLY_EVENT':
      return action.value
    case 'UNJOIN_EVENT':
      return action.value
    case 'UNJOIN_OTHER_EVENT':
      return action.value
    default:
      return state
  }
}

//
const memberEventDetailData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_EVENTDETAILDATA_FORMEMBER':
      return action.value
    default:
      return state
  }
}

//
const memberChatListData = (state = [], action) => {
  switch (action.type) {
    case 'MEMBER_GET_CHATLIST':
      return action.value
    default:
      return state
  }
}

//
const isEnable = (state = false, action) => {
  switch (action.type) {
    case 'SWITCHBUTTON_ENABLE':
      console.log(action)
      return action.value
    default:
      return state
  }
}

//reducer()
const eventReducer = combineReducers({
  eventData,
  eventDataForMap,
  eventTypeData,
  eventDetailData,
  memberEventDetailData,
  memberEventDataSelf,
  memberActionResponse,
  memberChatListData,
  isEnable,
})
export { eventReducer }
