import { combineReducers } from 'redux'

//
const mapmarksGCS = (state = [], action) => {
  switch (action.type) {
    case 'GET_GCSDATA':
      return action.value
    default:
      return state
  }
}
//
const Regions = (state = [], action) => {
  switch (action.type) {
    case 'GET_REGIONDATA':
      return action.value
    default:
      return state
  }
}
//
const LocusInfos = (state = [], action) => {
  switch (action.type) {
    case 'GET_INFODATA':
      return action.value
    default:
      return state
  }
}
//URL
const LocusImages = (state = [], action) => {
  switch (action.type) {
    case 'GET_LOCUSIMAGES':
      return action.value
    default:
      return state
  }
}
//
const LocusSeastate = (state = [], action) => {
  switch (action.type) {
    case 'GET_SEASTATES':
      return action.value
    default:
      return state
  }
}
//
const LocusWeeklyreport = (state = [], action) => {
  switch (action.type) {
    case 'GET_WEEKREPORT':
      return action.value
    default:
      return state
  }
}
const LocusTidereport = (state = [], action) => {
  switch (action.type) {
    case 'GET_TIDEREPORT':
      return action.value
    default:
      return state
  }
}
//reducer()
const locationReducer = combineReducers({
  mapmarksGCS,
  Regions,
  LocusInfos,
  LocusImages,
  LocusSeastate,
  LocusWeeklyreport,
  LocusTidereport,
})
export { locationReducer }
