import React, { useState, useEffect } from 'react'
import io from 'socket.io-client'

function Chat() {
  const [ws, setWs] = useState(null)

  const connectWebSocket = () => {
    //
    setWs(io('http://localhost:5555'))
  }

  useEffect(() => {
    if (ws) {
      // console 
      console.log('success connect!')
      //
      initWebSocket()
    }
  }, [ws])

  const initWebSocket = () => {
    // getMessage  server  getMessage 
    ws.on('getMessage', message => {
      console.log(message)
    })
    ws.on('getMessageAll', message => {
      console.log(message)
    })
    //
    ws.on('addRoom', message => {
      console.log(message)
    })
  }

  const sendMessage = () => {
    // emit  getMessage  server 
    ws.emit('getMessage', ' client')
  }

  const sendMessageAll = () => {
    ws.emit('getMessageAll', '')
  }

  // id  Server
  const changeRoom = event => {
    let room = event.target.value
    if (room !== '') {
      ws.emit('addRoom', room)
    }
  }

  return (
    <>
      <select onChange={changeRoom}>
        <option value=""></option>
        <option value="room1"></option>
        <option value="room2"></option>
      </select>
      <button
        type="button"
        class="btn btn-primary"
        onClick={() => {
          connectWebSocket()
        }}
      >
        
      </button>
      <button
        type="button"
        class="btn btn-primary"
        onClick={() => {
          sendMessage()
        }}
      >
        
      </button>
      <button
        type="button"
        class="btn btn-primary"
        onClick={() => {
          sendMessageAll()
        }}
      >
        
      </button>
      <button type="button" class="btn btn-primary">
        
      </button>
    </>
  )
}

export default Chat
