import { combineReducers } from 'redux'

//
const itemData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_ITEMDATA':
      return action.value
    default:
      return state
  }
}

//
const asideData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_ASIDEDATA':
      return action.value
    default:
      return state
  }
}

//
const itemDetailData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_ITEMDETAILDATA':
      return action.value
    default:
      return state
  }
}

//+1
const clickUpdateData = (state = {}, action) => {
  switch (action.type) {
    case 'CLICK_UPDATE':
      return action.value
    default:
      return state
  }
}

//reducer()
const itemReducer = combineReducers({
  itemData,
  itemDetailData,
  asideData,
  clickUpdateData,
})
export { itemReducer }
