import React, { useState, useEffect } from 'react'
import Select from 'react-select'

import SweetAlert from './Sweetalert2'

/*
    
    cityData = 
    distData= 
    typeData= 
    levelData= 
    addClassDataResponse= 

    
    addClassData= action
    handleGetDistData= action
    handleGetLevelData= action

    2020-03-22

*/

function AddClassContent(props) {
  const [addressCity, setAddressCity] = useState('') //
  const [addressDist, setAddressDist] = useState('') //
  const [response, setResponse] = useState(false) //response

  //state
  const [className, setClassName] = useState('')
  const [classTypeId, setClassTypeId] = useState('')
  const [classLevelId, setClassLevelId] = useState('')
  const [classLocation, setClassLocation] = useState('')
  const [classFullLocation, setClassFullLocation] = useState('')
  const [classStartDate, setClassStartDate] = useState('')
  const [classEndDate, setClassEndDate] = useState('')
  const [classPrice, setClassPrice] = useState('')
  const [classIntroduction, setClassIntroduction] = useState('')
  const [classDesc, setClassDesc] = useState('')
  const [classMAXpeople, setClassMAXpeople] = useState('')
  const [classImg, setClassImg] = useState('')

  //state

  //response
  useEffect(() => {
    if (response) {
      if (props.addClassDataResponse.status === 201) {
        SweetAlert.success('!')
        setResponse(false)
      } else {
        SweetAlert.errorAlert(
          props.addClassDataResponse.status,
          props.addClassDataResponse.msg
        )
        setResponse(false)
      }
    }
  }, [response])

  //state[addressDist]setClassLocation
  useEffect(() => {
    setClassLocation(
      document.querySelector('input[name="classLocation"]').value
    )
  }, [addressDist])

  //
  const addClassData = async () => {
    const ClassFormData = {
      className,
      classTypeId,
      classLevelId,
      classLocation,
      classFullLocation,
      classStartDate,
      classEndDate,
      classPrice,
      classIntroduction,
      classDesc,
      classMAXpeople,
      classImg,
    }

    //
    SweetAlert.sendConfirm(
      '?',
      setResponse,
      true,
      props.addClassData,
      ClassFormData
    )
  }

  return (
    <>
      <div className="row addClassForm">
        <div className="col-xl-6">
          <form name="addClassForm">
            <div className="form-group">
              <label htmlFor=""></label>
              <input
                type="text"
                name="className"
                className="form-control"
                placeholder="15"
                onChange={event => {
                  setClassName(event.target.value) //state
                }}
              />
            </div>
            <div className="form-row">
              <div className="form-group col-md-6">
                <label htmlFor=""></label>
                <select
                  name="classTypeId"
                  id=""
                  className="form-control"
                  onChange={event => {
                    const type = event.target.value
                    props.handleGetLevelData(false, type)
                    setClassTypeId(event.target.value) //state
                  }}
                >
                  <option value=""></option>
                  {props.typeData.length > 0
                    ? props.typeData.map((value, index) => {
                        return (
                          <option value={value.classTypeId} key={index}>
                            {value.classTypeName}
                          </option>
                        )
                      })
                    : ''}
                </select>
              </div>
              <div className="form-group col-md-6">
                <label htmlFor=""></label>
                <select
                  name="classLevelId"
                  id=""
                  className="form-control"
                  onChange={event => {
                    setClassLevelId(event.target.value) //state
                  }}
                >
                  <option value=""></option>
                  {props.levelData.length > 0
                    ? props.levelData.map((value, index) => {
                        return (
                          <option value={value.classLevelId} key={index}>
                            {value.classLevel}
                          </option>
                        )
                      })
                    : ''}
                </select>
              </div>
              <div className="form-group col-md-3">
                <label htmlFor=""></label>
                <select
                  name=""
                  className="form-control"
                  onChange={event => {
                    const city = event.target.value
                    props.handleGetDistData(city) //
                    setAddressCity(city) //()state
                    setAddressDist('')
                    setClassFullLocation('')
                    setClassLocation('')
                    document.querySelector(
                      'input[name="classFullLocation"]'
                    ).value = ''
                  }}
                >
                  <option value=""></option>
                  {props.cityData.length > 0
                    ? props.cityData.map((value, index) => {
                        return (
                          <option value={value.name} key={index}>
                            {value.name}
                          </option>
                        )
                      })
                    : ''}
                </select>
              </div>
              <div className="form-group col-md-3">
                <label htmlFor=""></label>
                <select
                  name=""
                  id=""
                  className="form-control"
                  onChange={event => {
                    const dist = event.target.value
                    setAddressDist(dist) //state
                    setClassFullLocation('')
                    setClassLocation('')
                    document.querySelector(
                      'input[name="classFullLocation"]'
                    ).value = ''
                  }}
                >
                  <option value=""></option>
                  {props.distData.length > 0
                    ? props.distData.map((value, index) => {
                        return (
                          <option value={value} key={index}>
                            {value}
                          </option>
                        )
                      })
                    : ''}
                </select>
              </div>
            </div>
            <div className="form-row">
              <div className="form-group col-md-3">
                <input
                  type="text"
                  name="classLocation"
                  className="form-control"
                  placeholder=""
                  value={addressCity + addressDist}
                  readOnly
                />
              </div>
              <div className="form-group col-md-6">
                <input
                  type="text"
                  name="classFullLocation"
                  className="form-control"
                  placeholder=""
                  onChange={event => {
                    setClassFullLocation(
                      addressCity + addressDist + event.target.value
                    ) //()state
                  }}
                />
              </div>
            </div>
            <div className="form-row">
              <div className="form-group col-md-6">
                <label htmlFor=""></label>
                <input
                  type="datetime-local"
                  name="classStartDate"
                  className="form-control"
                  onChange={event => {
                    setClassStartDate(event.target.value) //state
                  }}
                />
              </div>
              <div className="form-group col-md-6">
                <label htmlFor=""></label>
                <input
                  type="datetime-local"
                  name="classEndDate"
                  className="form-control"
                  onChange={event => {
                    setClassEndDate(event.target.value) //state
                  }}
                />
              </div>
            </div>
            <div className="form-row">
              <div className="form-group col-md-6">
                <label htmlFor=""></label>
                <input
                  type="number"
                  name="classPrice"
                  className="form-control"
                  onChange={event => {
                    setClassPrice(event.target.value) //state
                  }}
                />
              </div>
              <div className="form-group col-md-6">
                <label htmlFor=""></label>
                <input
                  type="number"
                  name="classMAXpeople"
                  className="form-control"
                  onChange={event => {
                    setClassMAXpeople(event.target.value) //state
                  }}
                />
              </div>
            </div>
            <div className="form-group">
              <label htmlFor=""></label>
              <input
                type="file"
                name="classImg"
                className="form-control-file"
                onChange={event => {
                  setClassImg(event.target.files[0]) //state
                }}
              />
            </div>
            <div className="form-group">
              <label htmlFor="">(70)</label>
              <textarea
                className="form-control"
                name="classIntroduction"
                rows="3"
                onChange={event => {
                  setClassIntroduction(event.target.value) //state
                }}
              ></textarea>
            </div>
          </form>
        </div>
        <div className="col-xl-6">
          <div className="form-group classDescBox">
            <label htmlFor="">(3000)</label>
            <textarea
              className="form-control"
              name="classDesc"
              rows="5"
              onChange={event => {
                setClassDesc(event.target.value) //state
              }}
            ></textarea>
          </div>
        </div>
      </div>
      <div className="col-12 btn-box d-flex">
        <button
          type="button"
          className="btn btn-outline-warning"
          onClick={() => {
            addClassData() //
          }}
        >
          
        </button>
      </div>
    </>
  )
}

export default AddClassContent
