import { combineReducers } from 'redux'

//
const classTypeData = (state = [], action) => {
  switch (action.type) {
    case 'GET_TYPEDATA':
      return action.value
    default:
      return state
  }
}
//
const classData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_CLASSDATA':
      return action.value
    default:
      return state
  }
}
//
const classDetailData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_CLASSDETAIDATA':
      return action.value
    default:
      return state
  }
}

//
const memberClassActionResponse = (state = {}, action) => {
  switch (action.type) {
    case 'APPLY_CLASS':
      return action.value
    case 'UNJOIN_CLASS':
      return action.value
    default:
      return state
  }
}

//
const memberClassData = (state = {}, action) => {
  switch (action.type) {
    case 'MEMBER_GETCLASSDATA':
      return action.value
    default:
      return state
  }
}

//reducer()
const classReducer = combineReducers({
  classData,
  classTypeData,
  classDetailData,
  memberClassData,
  memberClassActionResponse,
})
export { classReducer }
