import React from 'react'

/*
   props.classTypeData = 
   props.getClassData() = 
  2020-03-21
*/
function ClassListCategory(props) {
  //active
  function typeMenuActive(event) {
    document
      .querySelector('div.classCategoryList div')
      .classList.remove('rwdMenuShow')
    //li
    let subMenuLiList = document.querySelectorAll('ul[class="subMenu"] li')
    subMenuLiList.forEach(value => {
      value.classList.remove('active') //active
    })
    //a
    let levelTitleList = document.querySelectorAll(
      'ul[class="typeMenu"] li span'
    )
    levelTitleList.forEach(value => {
      value.classList.remove('active') //active
    })
    event.target.classList.add('active') //active
  }

  return (
    <>
      <div className="col-xl-3 col-sm-12 classCategoryList">
        <div>
          <h2></h2>
          <ul className="typeMenu">
            <li>
              <span
                data-type=""
                className="levelTitle"
                onClick={event => {
                  typeMenuActive(event)
                  props.getClassData()
                }}
              >
                
              </span>
            </li>
            <li>
              <span
                data-type="AIDA "
                className="levelTitle"
                onClick={event => {
                  typeMenuActive(event)
                  props.getClassData()
                }}
              >
                AIDA 
              </span>
              <ul className="subMenu">
                {props.classTypeData.map((value, index) => {
                  if (value.classTypeId === 'classTypeAIDA') {
                    return (
                      <li
                        key={index}
                        data-type={value.classTypeName}
                        data-level={value.classLevel}
                        onClick={event => {
                          typeMenuActive(event)
                          props.getClassData()
                        }}
                      >
                        {value.classLevel}
                      </li>
                    )
                  }
                })}
              </ul>
            </li>
            <li>
              <span
                data-type="SSI "
                className="levelTitle"
                onClick={event => {
                  typeMenuActive(event)
                  props.getClassData()
                }}
              >
                SSI 
              </span>
              <ul className="subMenu">
                {props.classTypeData.map((value, index) => {
                  if (value.classTypeId === 'classTypeSSI') {
                    return (
                      <li
                        key={index}
                        data-type={value.classTypeName}
                        data-level={value.classLevel}
                        onClick={event => {
                          typeMenuActive(event)
                          props.getClassData()
                        }}
                      >
                        {value.classLevel}
                      </li>
                    )
                  }
                })}
              </ul>
            </li>
            <li>
              <span
                data-type="PADI "
                className="levelTitle"
                onClick={event => {
                  typeMenuActive(event)
                  props.getClassData()
                }}
              >
                PADI 
              </span>
              <ul className="subMenu">
                {props.classTypeData.map((value, index) => {
                  if (value.classTypeId === 'calssTypePADI') {
                    return (
                      <li
                        key={index}
                        data-type={value.classTypeName}
                        data-level={value.classLevel}
                        onClick={event => {
                          typeMenuActive(event)
                          props.getClassData()
                        }}
                      >
                        {value.classLevel}
                      </li>
                    )
                  }
                })}
              </ul>
            </li>
            <li>
              <span
                data-type=""
                className="levelTitle"
                onClick={event => {
                  typeMenuActive(event)
                  props.getClassData()
                }}
              >
                
              </span>
              <ul className="subMenu">
                {props.classTypeData.map((value, index) => {
                  if (value.classTypeId === 'classType01') {
                    return (
                      <li
                        key={index}
                        data-type={value.classTypeName}
                        data-level={value.classLevel}
                        onClick={event => {
                          typeMenuActive(event)
                          props.getClassData()
                        }}
                      >
                        {value.classLevel}
                      </li>
                    )
                  }
                })}
              </ul>
            </li>
          </ul>
        </div>
      </div>
    </>
  )
}

export default ClassListCategory
