import React, { useState, useEffect } from 'react'
import SwitchButton from '../event/MemberEventComponents/SwitchButton'

/*
   props.getClassData() = 
          props.setIsEnable() = 

   props.isEnable = 
  2020-03-26
*/
function ClassSortBar(props) {
  const [rwdMenuShow, setRwdMenuShow] = useState(false)

  useEffect(() => {
    if (rwdMenuShow) {
      document
        .querySelector('div.classCategoryList div')
        .classList.add('rwdMenuShow')
    } else {
      document
        .querySelector('div.classCategoryList div')
        .classList.remove('rwdMenuShow')
    }
  }, [rwdMenuShow])

  const toggleSwitchButton = () => {
    props.setIsEnable(!props.isEnable)
  }

  const handleRWDmenu = () => {}

  return (
    <>
      <div className="row">
        <div className="col-2">
          <span
            class="material-icons d-md-none"
            onClick={() => {
              setRwdMenuShow(!rwdMenuShow)
            }}
          >
            filter_list
          </span>
        </div>
        <div className="col-10 col-xl-12 d-flex justify-content-end">
          <div className="d-flex switchbutton-jy align-items-center justify-content-end">
            <p></p>
            <SwitchButton
              type="button"
              active={props.isEnable}
              clicked={toggleSwitchButton}
            />
          </div>
          <div className="sortSelect ">
            <select
              name="sort"
              className="form-control"
              onChange={() => {
                props.getClassData()
              }}
            >
              <option value=""></option>
              <option value="classPrice,desc"></option>
              <option value="classPrice,asc"></option>
              <option value="classNOWpeople,desc"></option>
              <option value="classNOWpeople,asc"></option>
              <option value="classStartDate,asc"></option>
              <option value="classStartDate,desc"></option>
            </select>
          </div>
        </div>
      </div>
    </>
  )
}

export default ClassSortBar
