import { combineReducers } from 'redux'

//
const blogData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_BLOGDATA':
      return action.value
    default:
      return state
  }
}

//
const blogCommentsData = (state = {}, action) => {
  switch (action.type) {
    case 'GET_BLOGCOMMENTSDATA':
      return action.value
    default:
      return state
  }
}


//
const contentCommentsData = (state = {}, action) => {
  switch (action.type) {
    case 'ADD_CONTENTCOMMENTSDATA':
      return action.value
    default:
      return state
  }
}

//
const contentData = (state = {}, action) => {
  switch (action.type) {
    case 'ADD_CONTENTDATA':
      return action.value
    default:
      return state
  }
}

//
const editContentData = (state = {}, action) => {
  switch (action.type) {
    case 'EDIT_CONTENTDATA':
      return action.value
    default:
      return state
  }
}

//reducer()
const blogReducer = combineReducers({
  blogData,
  blogCommentsData,
  contentCommentsData,
  contentData,
  editContentData,
})
export { blogReducer }
