import React, { useState, useEffect } from 'react'
import SweetAlert from '../../class/SellerClassComponents/Sweetalert2'

//redux
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

//action
import {
  getEventTypeDataAsync,
  addEventDataAsync,
} from '../../../actions/event/event_Actions'
import {
  getCityDataAsync,
  getDistDataAsync,
} from '../../../actions/seller/index'

function AddEventContent(props) {
  const [addressCity, setAddressCity] = useState('') //
  const [addressDist, setAddressDist] = useState('') //
  const [response, setResponse] = useState(false) //response

  //state
  const [eventName, setEventName] = useState('')
  const [eventType, setEventType] = useState('')
  const [eventTypeId, setEventTypeId] = useState('')
  const [eventLocation, setEventLocation] = useState('')
  const [eventFullLocation, setEventFullLocation] = useState('')
  const [eventStartDate, setEventStartDate] = useState('')
  const [eventEndDate, setEventEndDate] = useState('')
  const [eventDesc, setEventDesc] = useState('')
  const [eventNeedPeople, setEventNeedPeople] = useState('')
  const [eventImg, setEventImg] = useState('')

  useEffect(() => {
    props.getEventTypeDataAsync()
    props.getCityDataAsync()
  }, [])

  //response
  useEffect(() => {
    if (response) {
      if (props.memberActionResponse.status === 201) {
        SweetAlert.success('!')
        setResponse(false)
      } else {
        SweetAlert.errorAlert(
          props.memberActionResponse.status,
          props.memberActionResponse.msg
        )
        setResponse(false)
      }
    }
  }, [response])

  //state[addressDist]setEventLocation
  useEffect(() => {
    setEventLocation(
      document.querySelector('input[name="eventLocation"]').value
    )
  }, [addressDist])

  //
  const addEventData = async () => {
    const EventFormData = {
      eventName,
      eventTypeId,
      eventType,
      eventLocation,
      eventFullLocation,
      eventStartDate,
      eventEndDate,
      eventDesc,
      eventNeedPeople,
      eventImg,
    }
    console.log(EventFormData)
    //
    SweetAlert.sendConfirm(
      '?',
      setResponse,
      true,
      props.addEventDataAsync,
      EventFormData
    )
  }

  return (
    <>
      <div className="addClassForm">
        <div className="row">
          <div className="col-xl-6 px-5">
            <form>
              <div className="form-group">
                <label htmlFor=""></label>
                <input
                  type="text"
                  name="className"
                  className="form-control"
                  placeholder="15"
                  onChange={event => {
                    setEventName(event.target.value) //state
                  }}
                />
              </div>
              <div className="form-row">
                <div className="form-group col-md-6">
                  <label htmlFor=""></label>
                  <select
                    name="classTypeId"
                    className="form-control"
                    onChange={event => {
                      const index = event.target.selectedIndex
                      setEventType(event.target.options[index].textContent)
                      setEventTypeId(event.target.value) //state
                    }}
                  >
                    <option value=""></option>
                    {props.eventTypeData.length > 0
                      ? props.eventTypeData.map((value, index) => {
                          return (
                            <option value={value.eventTypeId} key={index}>
                              {value.eventTypeName}
                            </option>
                          )
                        })
                      : ''}
                  </select>
                </div>
              </div>
              <label htmlFor=""></label>
              <div className="form-row">
                <div className="form-group col-md-5">
                  <select
                    className="form-control"
                    onChange={event => {
                      const city = event.target.value
                      props.getDistDataAsync(city) //
                      setAddressCity(city) //()state
                      setAddressDist('')
                      setEventFullLocation('')
                      setEventLocation('')
                      document.querySelector(
                        'input[name="eventFullLocation"]'
                      ).value = ''
                    }}
                  >
                    <option value=""></option>
                    {props.cityData.length > 0
                      ? props.cityData.map((value, index) => {
                          return (
                            <option value={value.name} key={index}>
                              {value.name}
                            </option>
                          )
                        })
                      : ''}
                  </select>
                </div>
                <div className="form-group col-md-5">
                  <select
                    className="form-control"
                    onChange={event => {
                      const dist = event.target.value
                      setAddressDist(dist) //state
                      setEventFullLocation('')
                      setEventLocation('')
                      document.querySelector(
                        'input[name="eventFullLocation"]'
                      ).value = ''
                    }}
                  >
                    <option value=""></option>
                    {props.distData.length > 0
                      ? props.distData.map((value, index) => {
                          return (
                            <option value={value} key={index}>
                              {value}
                            </option>
                          )
                        })
                      : ''}
                  </select>
                </div>
              </div>
              <div className="form-row">
                <div className="form-group col-md-4">
                  <input
                    type="text"
                    name="eventLocation"
                    className="form-control"
                    placeholder=""
                    value={addressCity + addressDist}
                    readOnly
                  />
                </div>
                <div className="form-group col-md-8">
                  <input
                    type="text"
                    name="eventFullLocation"
                    className="form-control"
                    placeholder=""
                    onChange={event => {
                      setEventFullLocation(
                        addressCity + addressDist + event.target.value
                      ) //()state
                    }}
                  />
                </div>
              </div>
              <div className="form-row">
                <div className="form-group col-md-12">
                  <label htmlFor=""></label>
                  <input
                    type="datetime-local"
                    name="classStartDate"
                    className="form-control"
                    onChange={event => {
                      setEventStartDate(event.target.value) //state
                    }}
                  />
                </div>
                <div className="form-group col-md-12">
                  <label htmlFor=""></label>
                  <input
                    type="datetime-local"
                    name="classEndDate"
                    className="form-control"
                    onChange={event => {
                      setEventEndDate(event.target.value) //state
                    }}
                  />
                </div>
              </div>
              <div className="form-row">
                <div className="form-group col-md-6">
                  <label htmlFor=""></label>
                  <input
                    type="number"
                    name="classMAXpeople"
                    className="form-control"
                    onChange={event => {
                      setEventNeedPeople(event.target.value) //state
                    }}
                  />
                </div>
              </div>
              <div className="form-group">
                <label htmlFor=""></label>
                <input
                  type="file"
                  name="classImg"
                  className="form-control-file"
                  onChange={event => {
                    setEventImg(event.target.files[0]) //state
                    document.getElementById(
                      'blah-JY'
                    ).src = window.URL.createObjectURL(event.target.files[0])
                  }}
                />
              </div>
            </form>
          </div>
          <div className="col-xl-6 px-5">
            <div className="form-group classDescBox">
              <label htmlFor="">(3000)</label>
              <textarea
                className="form-control"
                name="classDesc"
                rows="5"
                onChange={event => {
                  setEventDesc(event.target.value) //state
                }}
              ></textarea>
            </div>
          </div>
          <div className="col-12 d-flex justify-content-center mb-3">
            <img src="" alt="" id="blah-JY" />
          </div>
          <div className="col-12 btn-box d-flex">
            <button
              type="button"
              className="btn btn-outline-warning"
              onClick={() => {
                addEventData() //
              }}
            >
              
            </button>
          </div>
        </div>
      </div>
    </>
  )
}

// Reduxstore
const mapStateToProps = store => {
  return {
    cityData: store.sellerReducer.cityData,
    distData: store.sellerReducer.distData,
    eventTypeData: store.eventReducer.eventTypeData,
    memberActionResponse: store.eventReducer.memberActionResponse,
  }
}

// dispatchaction creators
const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      addEventDataAsync,
      getCityDataAsync,
      getDistDataAsync,
      getEventTypeDataAsync,
    },
    dispatch
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(AddEventContent)
