import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)

class SweetAlert {
  //
  /*
    title = 
    setStateFunc = state
    stateData = 
    callback = function
    callBackData = function1
    callBackData2 = function2
  */
  static sendConfirm(
    title,
    setStateFunc,
    stateData,
    callback,
    callBackData = '',
    callBackData2 = ''
  ) {
    MySwal.fire({
      title: title,
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: '',
    }).then(async result => {
      if (result.value) {
        if (callback) await callback(callBackData, callBackData2)
        if (setStateFunc && stateData) setStateFunc(stateData)
      }
    })
  }
  //
  // title = 
  static success(title) {
    MySwal.fire({
      icon: 'success',
      title: title,
      showConfirmButton: false,
      timer: 1500,
    })
  }

  //
  /*
    errorCode = 
    errorMsg = 
  */
  static errorAlert(errorCode, errorMsg) {
    MySwal.fire({
      icon: 'error',
      title: `${errorCode}`,
      text: `${errorMsg}`,
    })
  }

  //
  /*
    Steps = 
    queueMsg = (..)
    setStateFunc = state
    stateData = 
    callback = function
    callbackData = function
  */
  static questionAlert(
    Steps,
    queueMsg,
    setStateFunc,
    stateData,
    callback = '',
    callbackData = ''
  ) {
    MySwal.mixin({
      confirmButtonText: ' &rarr;',
      showCancelButton: true,
      progressSteps: Steps,
    })
      .queue(queueMsg)
      .then(async result => {
        if (result.value) {
          const inputData = result.value[0]
          if (callback) await callback(callbackData, inputData)
          if (setStateFunc && stateData) setStateFunc(stateData)
        }
      })
  }
}

export default SweetAlert
