import React, { useEffect, useState } from 'react'
import SwitchButton from '../../components/event/MemberEventComponents/SwitchButton'

//redux
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

//action
import { memberGetClassDataAsync } from '../../actions/class/class_Actions'

/*
  store
  memberClassData = 
  memberUnJoinClassResponse = 

  storeaction
  memberGetClassDataAsync = 
  memberUnJoinClassAsync = 
*/

import EventPageButtons from '../../components/event/EventPageButtons' //
import MemberClassList from '../../components/class/MemberClassComponents/MemberClassList' //
import Loading from '../../components/class/Loading' //
import '../../style/HS.scss'

function MemberClass(props) {
  const [hasLoading, setHasLoading] = useState(true) //
  const [isEnable, setIsEnable] = useState(false) // ""

  //state
  useEffect(() => {
    //true
    setHasLoading(true)
    setTimeout(() => {
      if (props.memberClassData.status) {
        setHasLoading(false)
      }
    }, 500)
  }, [props.memberClassData])

  //
  useEffect(() => {
    getMemberClassData()
  }, [isEnable])

  //SwitchButtonstate
  const toggleSwitchButton = () => {
    setIsEnable(!isEnable)
  }

  //
  const getMemberClassData = page => {
    //select
    const sort = document.querySelector('select[name="sort"]').value
    props.memberGetClassDataAsync(sort, page, isEnable)
  }

  return (
    <>
      {/* <!-- Orger Content --> */}
      <div className="container hsclass">
        <div className="row">
          <div className="col-lg-12">
            <div className="order-select d-flex justify-content-end">
              <div className="d-flex switchbutton-jy align-items-center">
                <p></p>
                <SwitchButton
                  type="button"
                  active={isEnable}
                  clicked={toggleSwitchButton}
                />
              </div>
              <select
                name="sort"
                className="form-control col-sm-2 select-jy"
                onChange={() => {
                  getMemberClassData()
                }}
              >
                <option value=""></option>
                <option value="classNOWpeople,desc"></option>
                <option value="classNOWpeople,asc"></option>
                <option value="classStartDate,asc"></option>
                <option value="classStartDate,desc"></option>
              </select>
            </div>
            <div className="order-history">
              {hasLoading ? (
                <Loading />
              ) : (
                <MemberClassList
                  memberClassData={props.memberClassData}
                  memberGetClassDataAsync={props.memberGetClassDataAsync}
                />
              )}
            </div>
            {/* <!-- pagination --> */}
            {hasLoading ? (
              ''
            ) : (
              <EventPageButtons
                totalPages={props.memberClassData.totalPages}
                getDataFromServer={getMemberClassData}
              />
            )}

            {/* <!-- end of pagination --> */}
          </div>
        </div>
      </div>
    </>
  )
}

// Reduxstore
const mapStateToProps = store => {
  return {
    memberClassData: store.classReducer.memberClassData,
  }
}

// dispatchaction creators
const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      memberGetClassDataAsync,
    },
    dispatch
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(MemberClass)
