import React, { useEffect, useState } from 'react'

// props.totalPages = 
// props.getDataFromServer() = 
//classevent
//2020-03-21
function EventPageButtons(props) {
  const [pageButtons, setPageButtons] = useState([]) //page

  //totalPages
  //<li>state.map
  useEffect(() => {
    let nowPageButton = []
    for (let i = 1; i <= props.totalPages; i++) {
      nowPageButton.push(
        <li
          className="page-item JY-classPage"
          key={i}
          data-page={i}
          onClick={event => {
            const page = event.target.textContent
            props.getDataFromServer(page)
          }}
        >
          <span className="page-link">{i}</span>
        </li>
      )
    }
    setPageButtons(nowPageButton)
  }, [props.totalPages])

  return (
    <>
      <nav aria-label="Page navigation example">
        <ul className="pagination justify-content-end">
          <li className="page-item page-prev">
            <span className="page-link">Previous</span>
          </li>
          {pageButtons.map(value => {
            return value
          })}
          <li className="page-item page-next">
            <span className="page-link">Next</span>
          </li>
        </ul>
      </nav>
    </>
  )
}

export default EventPageButtons
