import React, { useState, useEffect } from 'react'
import SwitchButton from './MemberEventComponents/SwitchButton'

// props.getEventData() = 
// props.setIsEnable() = 
// props.isEnable() = 
//2020-03-25
function EventSearchBar(props) {
  //SwitchButtonstate
  const toggleSwitchButton = () => {
    props.setIsEnable(!!props.isEnable)
  }
  return (
    <>
      <div className="row d-flex searchBox">
        <div className="col-xl-3 col-12">
          <div className="d-flex switchbutton-jy align-items-center justify-content-end">
            <p></p>
            <SwitchButton
              type="button"
              active={props.isEnable}
              clicked={toggleSwitchButton}
            />
          </div>
        </div>
        <div className="col-xl-2 col-12 d-flex">
          <select
            name="type"
            className="form-control mx-2"
            onChange={() => {
              props.getEventData()
            }}
          >
            <option value=""></option>
            {props.eventTypeData.length > 0
              ? props.eventTypeData.map((value, index) => {
                  return (
                    <option value={value.eventTypeName} key={index}>
                      {value.eventTypeName}
                    </option>
                  )
                })
              : ''}
          </select>
        </div>
        <div className="col-xl-2 col-12 d-flex">
          <select
            name="sort"
            className="form-control mx-2"
            onChange={() => {
              props.getEventData()
            }}
          >
            <option value=""></option>
            <option value="eventStartDate,asc"></option>
            <option value="eventStartDate,desc"></option>
            <option value="eventNeedPeople,asc"></option>
            <option value="eventNeedPeople,desc"></option>
          </select>
        </div>
        <div className="col-xl col-12 d-flex">
          <div className="mx-2">
            <input
              type="text"
              className="form-control searchInput"
              placeholder=""
            ></input>
          </div>
          <div>
            <button
              type="button"
              className="btn-search btn btn-outline-secondary"
              onClick={() => {
                props.getEventData()
              }}
            >
              
            </button>
          </div>
        </div>
      </div>
    </>
  )
}

export default EventSearchBar
